/* Reset some default styling */
body, ul {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-image: url('/src/background.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
    font-family: Archivo, sans-serif;
    overflow-x: hidden;
}

/* Style the header */
header {
    position: relative; /* Add relative positioning */
    z-index: 10; /* Set a higher z-index value for the header */
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    font-family: 'Archivo', serif;
    color: #00FFFF;
}

.logo {
    max-width: 500px;
}

.header-text h1, .header-text p {
    margin: 0;
}

.header-text h1 {
    font-size: 60px;
}

.header-text p {
    font-size: 30px;
}

/* Style for the services section */
#services {
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 48px;
}

.service {
    padding: 20px;
    margin: 10px 0;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

.service h3 {
    font-size: 24px;
}

/* Style for the contact form section */
#contact {
    padding: 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

#contact h2 {
    font-size: 36px;
}

#contact form input,

#contact form textarea {
    width: calc(100% - 20px);
    padding: 10px;
    margin: 5px 0 20px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

#contact form input[type="submit"] {
    cursor: pointer;
}

/* Style for the footer */
footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    font-family: 'Archivo', sans-serif;
    font-size: 16px;
}

.social-icons img {
    width: 24px;
    height: auto;
    margin: 0 5px;
}

/* Add this rule to create a horizontal line */
.horizontal-line {
    border: none; /* Remove default border */
    height: 2px; /* Set the desired height for the line */
    background-color: #00FFFF; /* Set the line color */
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px; /* Adjust the margin as needed to create space */
    width: 85%;
}

.image-collage {
    width: 85%;
    height: 50%;
    border-radius: 50px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

/* Media query for responsiveness */
@media screen and (max-width: 600px) {
    header {
        flex-direction: column;
        align-items: flex-start;
    }

    .header-text h1 {
        font-size: 24px;
    }

    .header-text p {
        font-size: 14px;
    }
}